<script setup>
import DataService from "@/services/DataService";
import Pager from '../../Common/Pager.vue';
import { DynamicForm } from 'tg-controls_cli/dist/myLib.common';
</script>
<template>
    <div class="my-popups">
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <button type="button" class="close" @click=closepopup()>
                            <em aria-hidden="true" class="fa fa-times"></em>
                        </button>
                        <h4 name="header" class="modal-title">
                            {{ $t('Shareto') }}
                        </h4>
                    </div>
                    <div class="modal-body">
                    <dynamic-form lang="en" :buttons="buttons" ref="SharePopup"  :schema="FormSchema"
                      v-on:OnSubmit="onSubmit" >
                    </dynamic-form>

                  <div class="w-100 float-left mt-4" >
                    <div class="heading-border">
                    <h5 class="h5">
                      <span>{{ $t("SharedAlready") }}</span>
                    </h5>
                  </div>

                  <div class="col-12 p-0 mt-3 px-small-0">
                  <div class="form-group mb-3">
                    <input id="searchdata" maxlength="50" v-model="Search" class="form-control" type="text"
                      v-on:keyup="SearchData()" placeholder="Search by User or Role">
                  </div>
                </div>
                <div class="table-responsive table-fix-header mb-3">
                  <table id="example" class="table table-bordered mb-0 mt-0" style="width:100%"
                    aria-label="ShareUserRole listing">
                    <tbody>
                      <tr v-for="item in UserListingData.JsonData" :key="item"  v-if="item.USERNAME">
                        <td >
                          <div class="conversation-box_custreps">
                            <div class="name-square" v-if="(item.share_based_on == 'User')">
                              <em class="fa fa-user text-white fa-2x" aria-hidden="true"></em>
                            </div>
                            <div class="role-square" v-if="(item.share_based_on == 'Role')">
                              <em class="fa fa-user-circle text-white fa-2x" aria-hidden="true"></em>
                            </div>
                            <div class="conversation-header">
                              {{ item.USERNAME }}
                            </div>
                            <div class="conversation-header">
                              <small>
                                {{ item.share_based_on }}
                              </small>
                            </div>
                          </div>
                        </td>
                        <td class="text-right pr-3 single-action">
                          <a href="javascript:;" :title="$t('Delete')"   
                            v-on:click="shareroleListingDelete(item)">
                            <em class="fa fa-trash text-danger ml-4" v-if="TotalRecords>0"></em>
                          </a>
                        </td>
                        
                      </tr>
                      <tr v-if="NoRecordfound==true">
                        <td class="text-center text-danger no-record">No Record(s) Found.</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                <Pager v-bind:numberofrecords="TotalRecords" prev-text="Prev" first-text="First" last-text="Last"
                  next-text="Next" @pagerMethod="pagerMethod" v-on:change="SetPageSize"></Pager>
                </div>
            </div>
          </div>
        </div>
        </div>
    </div>
</template>
<script>


// import Multiselect from  'vue-multiselect'

export default {
    components: {
      DynamicForm,
      Pager},
    props: {
        CustomlayoutId: {
            type: Number,
            required: true
        },
       
    },
    data() {
        return {

            SharetoRole: [],
            shearedtypeRole: [],
            searchable: true,
            reqFrom: null,
            loggeduserid: '',
            companyId: '',
            pageSize: 10000,
            pageNum: 1,
            role_id: '',
            Options: [],
            Cloneheader: false,
            AssignRolePopup:false,
            RolesCheckData:[],
            RolesCheckReturnData:[],
            ShareToRoleitem:'',
            mode: 'tags',
            FormSchema: [{
            layoutType: "Single",
            Data: [{
            astype: "RadioField",
            label: 'Share with',
            name: "Sharewithtype",
            value: "User",
            placeholder: "",
            config: {
              options: [{
                value: "User",
                name: "User"
              },
              {
                value: "Role",
                name: "Role"
              }
              ],
              showAddIcon: false,
              onChange: this.SelectshareType
            },
              validationRules: "",
            },]
        },
        {
          layoutType: "double",
          Data: [{
            astype: "CustomSelectField",
            mode: "tag",
            label: this.$t('User '),
            name: "User",
            value: "",
            config: {
            options: [],
            showAddIcon: false,
            onChange: this.GetUSerId
            },
            placeholder: this.$t('SelectUser'),
            validationRules: { "required": true, max: 100 },
            },
            {
            astype: "CustomSelectField",
            mode: "tag",
            label: this.$t('Role '),
            name: "Role",
            value: "",
            config: {
            options: [],
            showAddIcon: false,
            onChange: this.GetRolesID
            },
            placeholder: this.$t('SelectUser'),
            visibility: false,
            validationRules: { "required": true, max: 100 },
            }],
            }
            ],
            buttons: [{
            type: "submit",
            class: "btn btn-success",
            text: "<i class='fa fa-save pr-2'></i>Save",
            },
            {
            type: "button",
            class: "btn btn-danger",
            text: "<i class='fa fa-close pr-2'></i>Cancel",
            onClick: this.closepopup
            }],
            AssignRolePopup: false,
            shareroleids:'',
            companyId: '',
            username: '',
            pageSize: 10000,
            RolesCheckData:[],
            sharedtoUser:true,
            sharedtoUserIds:[],
            sharedtoRoleIds:[],
            is_paged: 1,
            sortBy: '',
            sortExp: '',
            Page_Size: 10,
            PageNumber: 1,
            UserListingData: [],
            isLoading: false,
            TotalPages: 0,
            TotalRecords: 0,
            Search: '',
            shareddatainlist:false,
            NoRecordfound:false
        }
    },
    created() {
        var vm = this;
        vm.loggeduserid = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.FetchRoleData();
        vm.FetchData();
        vm.BindUserRoleListing();
    },
    methods: {
      SearchData: function () {
      var vm = this;
      var params = `username=${vm.Search}&is_paged=${vm.is_paged}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&pageSize=${vm.Page_Size}&pageNumber=${vm.PageNumber}&custom_layout_id=${vm.CustomlayoutId}`;
      DataService.ShareUserRoleListing(params).then(function (response) {
        vm.UserListingData = JSON.parse(response.data);
        if (vm.UserListingData.JsonData.length == 0) {
          vm.TotalRecords = [];
        }
        else {
          vm.TotalRecords = vm.UserListingData.JsonData[0].TOTAL_RECORDS;
        }
      })
    },
    BindUserRoleListing: function () {
      var vm = this;
      vm.isLoading = true
      var params = `username=${vm.username}&is_paged=${vm.is_paged}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&pageSize=${vm.Page_Size}&pageNumber=${vm.PageNumber}&custom_layout_id=${vm.CustomlayoutId}`;
      DataService.ShareUserRoleListing(params).then(function (response) {
        vm.UserListingData =  JSON.parse(response.data);
        if(vm.UserListingData.JsonData[0].length==0){
          vm.NoRecordfound = true;
        }
        else{
          vm.NoRecordfound = false;
        }
        if (vm.UserListingData.JsonData.length > 0) {
          vm.TotalRecords = vm.UserListingData.JsonData[0].TOTAL_RECORDS;
          vm.TotalPages = Math.ceil(vm.TotalRecords / vm.Page_Size);
          vm.isLoading = false
        }
        else {
          vm.TotalRecords = 0;
          vm.TotalPages = 0;
        }
      })
    },
    pagerMethod(params){
      var vm = this;
      vm.PageNumber=params.PageNumber;
      vm.Page_Size=params.PageSize;
      vm.BindUserRoleListing();
    },
    SetPageSize: function (event) {
      var vm = this;
      vm.isLoading = true;
      // vm.TotalRecords = 0;
      vm.Page_Size = event.target.value;
      vm.PageNumber = 1;
      vm.TotalPages = Math.ceil(vm.TotalRecords / vm.Page_Size);
      vm.BindUserRoleListing();
    },
        closeAssignpopup() {
            var vm = this;
            vm.AssignRolePopup = false;           
        },
        roleListingDelete: function (e) {
            var vm = this;
            vm.AssignRolePopup = false;
            vm.SharetoRole.pop()
        },
       
    SelectshareType(event, field) {
      var vm = this;
      vm.sharedtoRoleIds=[];
      field.value = event.target.getAttribute('data-value');
      if (field.value == 'User') {
        (vm.FormSchema)[1].Data[0].visibility = true;
        (vm.FormSchema)[1].Data[1].visibility = false;
        (vm.FormSchema)[1].Data[1].value ='';
        vm.FetchData()
      } else {
        (vm.FormSchema)[1].Data[0].visibility = false;
        (vm.FormSchema)[1].Data[1].visibility = true;
        (vm.FormSchema)[1].Data[0].value ='';
        (vm.FormSchema)[0].Data[0].value='Role'
        vm.FetchRoleData();
      }
   //   vm.$refs.formdata.validateForm.reset();
    },
    FetchData: function () {
      var vm = this;
      if ((vm.FormSchema)[0].Data[0].name = 'User') {
        vm.isLoading = true;
        DataService.UserDDL().then(response => {
          if (response.data != '' && response.data != null) {
            vm.fetchuserdata = response.data;
            vm.FormSchema[1].Data[0].config.options = []
            vm.fetchuserdata.forEach((item) => {
              (vm.FormSchema)[1].Data[0].config.options.push({
                value: item.userid,
                name: item.username
              })
            })
          }
        //   vm.$refs.formdata.UpdateKeyValue();
          vm.isLoading = false;
        })
      }
    },
    FetchRoleData: async function (e, field) {
            var vm = this;
            var params = `companyId=${vm.companyId}&pageSize=${vm.pageSize}&statusId=1001`;
            await DataService.Roledata(params).then(function (response) {
                if (response.data.length != null) {
                    vm.shearedtypeRole = response.data;
                    vm.shearedtypeRole.forEach((item, index) => {
                        (vm.FormSchema)[1].Data[1].config.options.push({
                            value: item.rolE_ID,
                            name: item.rolE_NAME
                        });
                    });
                }
            });
            vm.isLoading = false;
        },
        closepopup(item) {
            var vm = this;
            vm.$emit('closepopup', item);
            vm.$emit('refreshlist',item);
        },
        GetUSerId(e, field, uservalue){
        var vm = this;
        vm.sharedtoUser=true;
        vm.sharedtoUserIds.push(uservalue.value)
                    let tagIndex = vm.fetchuserdata.findIndex(x => x.userid == uservalue.value);
                    if (tagIndex > -1) {
                        vm.fetchuserdata.splice(tagIndex, 1);
                    }
        },
        GetRolesID(e, field, Rolevalue){
        var vm = this;
        vm.sharedtoUser=false;
        vm.sharedtoRoleIds.push(Rolevalue.value)
        let tagIndex = vm.shearedtypeRole.findIndex(x => x.rolE_ID == Rolevalue.value);
                    if (tagIndex > -1) {
                        vm.shearedtypeRole.splice(tagIndex, 1);
                    }
        },
        onSubmit: function () {
            var vm = this;
            var detailsIDs = [];
            if(vm.sharedtoUser==true){
                detailsIDs=vm.sharedtoUserIds;
            }
            else{
                detailsIDs=vm.sharedtoRoleIds;
            }
            var sharetoarray = [];
            if(vm.FormSchema[1].Data[0].value){
              vm.UserListingData.JsonData.forEach(items =>{
             vm.FormSchema[1].Data[0].value.forEach(data =>{
               if(data.label === items.USERNAME){
                sharetoarray.push(items.USERNAME);
                vm.ShowAlert(vm.$t('ConfirmShareduservalue',true,[sharetoarray]), 'warning', true, vm.$t('Alert'));
               }
             })
            })
            }
            else{
              vm.UserListingData.JsonData.forEach(items =>{
             vm.FormSchema[1].Data[1].value.forEach(data =>{
               if(data.label === items.USERNAME){
                sharetoarray.push(items.USERNAME);
                vm.ShowAlert(vm.$t('ConfirmSharedRolevalue',true,[sharetoarray]), 'warning', true, vm.$t('Alert'));
               }
             })
            })
            }
            var params = `custom_layout_id=${vm.CustomlayoutId}&rolesdetail=${detailsIDs}&share_based_on=${(vm.FormSchema)[0].Data[0].value}`;
            DataService.ModifyShareWithOnCustomLayout(params).then(function (response) {
            if (response.statusText == "OK") {
              //vm.closepopup();
              vm.BindUserRoleListing(); 
              }
            })
        },
        shareroleListingDelete: function (item) {
        var vm = this;
        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t((vm.FormSchema)[0].Data[0].value)), function () {
          var params = `Custom_layout_id=${item.custom_layout_id}&share_based_on=${item.share_based_on}&sharedwithId=${item.role_id}`
          DataService.DeleteShareUserRole(params).then(function (response) {
            if (response.data[0].Status == "Success") {
              vm.ShowAlert(vm.$t("ShareAndRole"), 'success', true, vm.$t('Alert'));
              vm.BindUserRoleListing();
            }
          });
        })
    },
    }
}
</script>