<template>
    <div>
        <div class="my-popups" >
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle  py-0">
                            <button type="button" class="close" @click=closepopup() >
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                            <h4 name="header" class="modal-title py-3">
                               Get Quick Add List
                            </h4>
                        </div>
                        <div class="container-fluid padding-t_10">
              <div class="row new_tab">
                <form class="w-100 p-4">
                  <div v-for="(item, index) in Quicklistdata" :key="index" class="collapse show mt-2 col-md-4">
                            <div class="btn-group btn-group-toggle custom-reports-icons my-2 w-100" data-toggle="buttons" v-on:click="getForm(item)" :key="item.sub_module_id">
                            <label v-bind:class="{ 'btn btn_cstm': true, 'active': (item.sub_module_id == selectedModuleId) }"
                                v-bind:for="'cls_usertype' + index">
                                <input type="radio" v-bind:value="item.sub_module_id" name="cls_usertype" v-bind:id="'cls_usertype' + index" v-model="selectedModuleId" />
                                <span class="d-block pb-2"><em class="fa fa-plus bg-transparent" aria-hidden="true"></em></span>
                                Add {{ $t(item.sub_module_name) }}
                            </label>
                        </div>
                    </div>
                </form>
              </div>
            </div>
           </div>
        </div>
      </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../services/DataService';
export default {
  props:{
    LayoutListingData:{
      type:Array,
      required:true
    }
  },
    // components: {
    //     manageformlayout
    // },
    data() {
        return {
            //isCollapsed: false,
            isLoading: false,
            Quicklistdata:[],
           // moduledata:[],
            // manageform:false,
            //getAlllayoutData:[],
            getvalues:{},
            selectedModuleId: null,
        }  
    },
     created() {
     var vm = this;
     vm.getQuickAddData();
    },
    methods: {
    getForm(value){
      var vm = this;
      vm.LayoutListingData.forEach(element => {
        if(value.custom_layout_id === element.CUSTOM_LAYOUT_ID)  {
          value.is_active_template=element.IS_ACTIVE_TEMPLATE
        }     
      })
    vm.$parent.getFormpopup(value);
    },
    getQuickAddData() {
    var vm = this;
    DataService.GetQuickAddDataListing().then((response) => {   
    vm.Quicklistdata = response.data.data;
    })
    },
    closepopup() {
    var vm = this;
    vm.$parent.closeQuickAddpopup();
    }
        // closeQuickAddLayoutform: function() {
        //     this.manageform= false
        // }
    }
 
}
</script>